export default {
  data() {
    return {
      city: "昆明市",
      hidden: true,
      listCurID: "",
      list: [],
      listCur: ""
    };
  },
  onLoad() {
    let list = [{}];
    for (let i = 0; i < 26; i++) {
      list[i] = {};
      list[i].name = String.fromCharCode(65 + i);
    }
    this.list = list;
    this.listCur = list[0];
  },
  onReady() {
    let that = this;
    uni
      .createSelectorQuery()
      .select(".indexBar-box")
      .boundingClientRect(function(res) {
        that.boxTop = res.top;
      })
      .exec();
    uni
      .createSelectorQuery()
      .select(".indexes")
      .boundingClientRect(function(res) {
        that.barTop = res.top;
      })
      .exec();
  },
  methods: {
    //获取文字信息
    getCur(e) {
      this.hidden = false;
      this.listCur = this.list[e.target.id].name;
    },
    setCur(e) {
      this.hidden = true;
      this.listCur = this.listCur;
    },
    //滑动选择Item
    tMove(e) {
      let y = e.touches[0].clientY,
        offsettop = this.boxTop,
        that = this;
      //判断选择区域,只有在选择区才会生效
      if (y > offsettop) {
        let num = parseInt((y - offsettop) / 20);
        this.listCur = that.list[num].name;
      }
    },

    //触发全部开始选择
    tStart() {
      this.hidden = false;
    },

    //触发结束选择
    tEnd() {
      this.hidden = true;
      this.listCurID = this.listCur;
    },
    indexSelect(e) {
      let that = this;
      let barHeight = this.barHeight;
      let list = this.list;
      let scrollY = Math.ceil((list.length * e.detail.y) / barHeight);
      for (let i = 0; i < list.length; i++) {
        if (scrollY < i + 1) {
          that.listCur = list[i].name;
          that.movableY = i * 20;
          return false;
        }
      }
    }
  }
};
